.header {
  border-bottom: 1px solid #d9dfe2;
  background-color: #17161c;
  margin-left: 1%;
  color: #fff;

  .header-logo {
    display: flex;
    flex: 1 1;
    font-weight: 600;
    margin-left: 50px;
    width: 50px;
    color: grey;
    a {
      color: #FFF;
      text-decoration: none;
    }
  }
  .header-logo button{
    background-color: #17161c;
    border: none;
  }

  .input{
    display: flex;
    margin-right: 250px;
  }

  .header-search {
    display: flex;
    flex: 5 1;
    .user-name {
      margin-right: 50px;
      cursor: default;
      user-select: none;
    }
    .profile-pic {
      height: 8px;
      width: 25px;
    }
  }
}
